import { useState } from "react";
import { useSnackbar } from "notistack";
// @mui
import {
  Container,
  TextField,
  Stack,
  Grid,
  FormControlLabel,
  Checkbox,
  Button,
  Typography,
  Alert,
} from "@mui/material";
import { LoadingButton } from "@mui/lab";
// routes
import { PATH_DASHBOARD } from "../../routes/paths";
// hooks
import useSettings from "../../hooks/useSettings";
// components
import Page from "../../components/Page";
import HeaderBreadcrumbs from "../../components/HeaderBreadcrumbs";
import { postCancelTicket } from "../../redux/slices/ticket";
// sections
import Ticket from "../../sections/@dashboard/ticket/details/CancelTicket";
import extractErrorMessage from "../../utils/error";
import { useTranslation } from "react-i18next";

export default function TicketCancel() {
  const { themeStretch } = useSettings();
  const { t } = useTranslation();
  const { enqueueSnackbar } = useSnackbar();

  const [currentTicket, setCurrentTicket] = useState(null);
  const [ticketId, setTicketId] = useState("");
  const [loading, setLoading] = useState(false);

  const [form, setForm] = useState({
    password: "",
    acceptTerms: false,
  });

  const [errors, setErrors] = useState([]);

  const handleChangeForm = (field) => (event) => {
    setForm((prev) => ({ ...prev, [field]: event.target.value }));
  };

  const handleGetObject = async () => {
    const newErrors = [];

    if (!ticketId) newErrors.push(t("remesas.ticket_required"));
    if (!form.password) newErrors.push(t("remesas.password_required"));
    if (!form.acceptTerms) newErrors.push(t("remesas.accept_required"));

    setErrors(newErrors);

    if (newErrors.length > 0) return;

    setLoading(true);
    try {
      const result = await postCancelTicket(String(ticketId), form.password);
      setCurrentTicket(result);
    } catch (error) {
      const data = error?.response?.data || {};

      const rawCode = data.code || data.error || error.code || "UNEXPECTED_ERROR";

      const providerMessage = data.message || data.detail || error.message || "";

      const i18nKey = `remesas.errors.${String(rawCode).toUpperCase()}`;

      const translated =
        t(i18nKey, {
          defaultValue: "",
        }) || "";

      const finalMessage = translated
        ? `${translated} (${providerMessage})`
        : providerMessage || t("remesas.errors.UNEXPECTED_ERROR");
      enqueueSnackbar(finalMessage, { variant: "error" });
      setCurrentTicket(null);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Page title={`Ticket: ${t("p_ticket.ticket_cancel")}`}>
      <Container maxWidth={themeStretch ? false : "lg"}>
        <HeaderBreadcrumbs
          heading={t("p_ticket.ticket_cancel")}
          links={[
            { name: t("params.dashboard"), href: PATH_DASHBOARD.root },
            { name: t("p_ticket.ticket"), href: PATH_DASHBOARD.ticket.root },
            { name: currentTicket?.full_name || "" },
          ]}
        />

        {errors.length > 0 && (
          <Alert severity="error" sx={{ mb: 3 }}>
            {errors.map((err, i) => (
              <div key={i}>{err}</div>
            ))}
          </Alert>
        )}

        <Stack
          direction={{ xs: "column", sm: "row" }}
          spacing={2}
          sx={{ mb: 4 }}
        >
          <TextField
            fullWidth
            required
            label={t("p_ticket.enter_ticket_id")}
            value={ticketId}
            onChange={(e) => setTicketId(e.target.value)}
            error={errors.some((e) => e.toLowerCase().includes("ticket"))}
          />

          <LoadingButton
            variant="contained"
            color="primary"
            onClick={handleGetObject}
            loading={loading}
            sx={{ px: 4 }}
          >
            {t("p_ticket.cancel")}
          </LoadingButton>
        </Stack>

        <Grid container spacing={2} sx={{ mb: 4 }}>
          <Grid item xs={12} md={6}>
            <TextField
              type="password"
              fullWidth
              required
              label={t("remesas.confirm.passwordLabel")}
              value={form.password}
              onChange={handleChangeForm("password")}
              error={errors.some(
                (e) =>
                  e.toLowerCase().includes("mot de passe") ||
                  e.toLowerCase().includes("password")
              )}
              helperText={
                errors.some(
                  (e) =>
                    e.toLowerCase().includes("mot de passe") ||
                    e.toLowerCase().includes("password")
                )
                  ? t("remesas.confirm.password_required")
                  : ""
              }
            />
          </Grid>

          <Grid
            item
            xs={12}
            md={6}
            sx={{ display: "flex", alignItems: "center" }}
          >
            <FormControlLabel
              control={
                <Checkbox
                  checked={form.acceptTerms}
                  onChange={(e) =>
                    setForm((prev) => ({
                      ...prev,
                      acceptTerms: e.target.checked,
                    }))
                  }
                />
              }
              label={
                <Typography
                  variant="body2"
                  sx={{
                    display: "flex",
                    gap: 0.5,
                    flexWrap: "wrap",
                  }}
                >
                  {t("remesas.confirm.acceptPrefix")}

                  <Button
                    variant="text"
                    color="primary"
                    size="small"
                    href="/#/termes-conditions"
                    target="_blank"
                    rel="noopener"
                    sx={{
                      p: 0,
                      minWidth: 0,
                      textTransform: "none",
                    }}
                  >
                    {t("remesas.confirm.termsLink")}
                  </Button>
                </Typography>
              }
            />
          </Grid>
        </Grid>

        {currentTicket && <Ticket ticket={currentTicket} />}
      </Container>
    </Page>
  );
}
